<template>
    <div class="userMessage  content">
     <!-- tiitle栏 -->
        <div style="padding-bottom:30px">
     
         <div class="top-title" style="background: #66B1ff; padding-left:2%; line-height: 50px;">
                <span class="el-icon-info"></span><span>系统管理</span><span>/后台用户管理</span>
         </div>
        </div>

    <!-- 搜索栏 -->
        <div class="top-search" style="text-align: center">
            <el-input style="margin: 0 auto; width:50%;" placeholder="请输入内容"   v-model="search" class="input-with-select">
                
                <el-button slot="append" @click="goSearch()" icon="el-icon-search"></el-button>
            </el-input>
        </div>
    <!-- 新增用户 -->
        <div  class="button-add" style=" display:flex;  justify-content: space-between; overflow: hidden; margin-top:20px; margin-left:2%; margin-right:2%">
         <el-button  style=" margin-right:5%;margin-bottom:20px" type="primary"  @click="deletMost()">批量删除</el-button>  
         <el-button  style=" margin-right:0%;margin-bottom:20px" type="primary"  @click="addTode()">新增用户</el-button> 
        </div>


        <el-table
            ref="multipleTable"
            :data="tableList"
            tooltip-effect="dark"
            style="width: 96%;margin:0 auto"
            border
            :header-cell-style="{background:'#EEF1F6'}"
            size="mini"
            @selection-change="handleSelectionChange">
            <el-table-column
            type="selection"
            >
            </el-table-column>
           <el-table-column
            type="index"
           >
            </el-table-column>
            <el-table-column
            prop="username"
            label="用户名"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="nickname"
            label="昵称"
            >
            </el-table-column>

            <el-table-column
            prop="dateTime"
            label="创建时间"
            >
            </el-table-column>

            <el-table-column
            fixed="right"
            label="操作"
            >
            <template slot-scope="scope">
                <el-button
                v-show="curentUserName==tableList[scope.$index].username ? false : true"
                @click.native.prevent="deleteRow(scope.$index, tableList)"
                type="text"
                size="small">
                删除
                </el-button>
                <el-button
                v-show="curentUserName==tableList[scope.$index].username ? false : true"
                @click.native.prevent="changeRow(scope.$index, tableList)"
                type="text"
                size="small">
                修改
                </el-button>
                <el-button
                v-show="curentUserName==tableList[scope.$index].username ? false : true"
                @click.native.prevent="resetPass(scope.$index, tableList)"
                type="text"
                size="small">
                重置密码
                </el-button>
            </template>
            </el-table-column>
        </el-table>
<!-- 分页 -->
          <div class="block" style="overflow:hidden">

                <el-pagination
                style="float:right; margin-right:10%"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="navigatepageNums"
                :page-size="100"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
                </el-pagination>
            </div>

    <!-- 弹窗 -->
        <el-dialog
        :title="popTitle"
        :visible.sync="dialogVisible"
        width="40%"
        @close="closeDialog"
        :modal-append-to-body='false'
        :close-on-click-modal="false"
        center>
        <el-form :model="ruleForm" :rules="rules"   ref="ruleForm"  size='medium'    class="demo-ruleForm">

            <el-form-item             
            prop="username"
>
                <el-input type="text" placeholder="请输入用户名" v-model.number="ruleForm.username" autocomplete="off">
                 <template slot="prepend"><i class="el-icon-edit"></i></template>
                </el-input>
            </el-form-item>

            <el-form-item             
            prop="nickname"
>
                <el-input type="text"  placeholder="请输入昵称" v-model.number="ruleForm.nickname" autocomplete="off">
                 <template slot="prepend"><i class="el-icon-edit"></i></template>
                </el-input>
            </el-form-item>


            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm',popTitle)" class="btn-submmit">{{popsubName}}</el-button>
                            
                </el-form-item>
                 
            </el-form>
        
        </el-dialog>  


        
    </div>
</template>
<script>

import url from '../utils/url';
import {getCookie} from '../utils/utils';
import {postRequest} from '../utils/api';
export default {

    
    data(){

        var validateUsername = (rule, value, callback) => {
            console.log("999999999",value)
        
        


        if (value === '') {
          callback(new Error('用户名不能为空'));
        }else{
           postRequest(url.userCheckName, {
                username:value
                   
                }).then(data=> {
                        console.log("校验用户名数据",data.data)
                        if(data.data.status==200){
                            callback();
                          
                          return

                        }else{
                          callback(new Error(`${data.data.msg}`));
                        }
                        
                     

            })
        }

      };
      var validateNickname = (rule, value, callback) => {
         if (value === '') {
          callback(new Error('昵称不能为空'));
        }else{
          callback();
        }

      };
        return{
            search:"",
            popTitle:"",
            popsubName:"",
            changeIndex:"",
            currentPage: 1,
            navigatepageNums:[],

            total:1,
            
            curentUserName:"",

            ruleForm: {
            username: '',
            nickname:'',
            },

            rules: {
                    username: [
                        { validator: validateUsername, trigger: 'blur' }
                    ],
                    nickname: [
                        { validator: validateNickname, trigger: 'blur' }
                    ],

                    },
            dialogVisible:false,
            tableList: [],
            multipleSelection: []
        }
    },
    methods:{


        handleSelectionChange(val) {
        this.multipleSelection = val;
        },
        // 点击弹窗的叉号
        closeDialog(){
        this.$refs['ruleForm'].resetFields();
        },
        // 增加用户按钮弹出框出现
        addTode(){

            this.dialogVisible=true
            this.popTitle="添加新用户"
            this.popsubName="确认添加"
            
             
            },

        //   修改弹窗
        changeRow(index){
                console.log("下标志",index)
                this.changeIndex=index
                this.dialogVisible=true
                this.ruleForm.username=this.tableList[index].username
                this.ruleForm.nickname=this.tableList[index].nickname
                this.popTitle="修改用户"
                this.popsubName="确认修改"
        },

        //重置密码
        resetPass(){
             postRequest(url.userResetPassword, {
                id:getCookie("userId")
                    
                }).then(data=> {
                        console.log("重置密码新数据",data.data)
                        if(data.data.status==200){
                         
                        //   this.$router.push({ path: '/login' })

                        }
                        this.$notify({
                            title: '修改状态',
                            message: data.data.msg,
                            type: `${data.data.status==200 ?"success" : "error"}`
                          });
                     

                })
        },
        // 提交。添加和修改写在一起
        submitForm(formName) {
        let subUrl="" //根据是修改还是增加来修改接口

        if(this.popTitle=="添加新用户"){ //添加用户的接口
            subUrl=url.userInsert
        }else{ //修改用户的接口
            subUrl=url.userUpdate
        }
        this.$refs[formName].validate((valid) => {
             
          if (valid) {
              let formV=this.$refs[formName].$options.propsData.model;
            console.log("444444444444444",formV)
             postRequest(subUrl, {
                    id: this.changeIndex ?  this.tableList[this.changeIndex].id : this.changeIndex, //如果是修改就传ID
                    username: formV.username,
                    nickname: formV.nickname,
                    pageNum:this.currentPage
                }).then(data=> {
                        this.$refs['ruleForm'].resetFields();
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                        this.dialogVisible=false
                     
                       
                                      

                })
          } else {

            return false;
          }
        });
       
      },       







    

        deletMost(){
            console.log("查看表单内的选中的条数",this.multipleSelection)
            let chooseStrId="";
            let chooseStrPictures="";
            for(let i=0;i<this.multipleSelection.length;i++){

                chooseStrId+=this.multipleSelection[i].id+","
            }
            chooseStrId = chooseStrId.substr(0, chooseStrId.length - 1);
            console.log("删除的chooseStrId",chooseStrId);
            postRequest(url.userDelete, {
                   id:chooseStrId,
                   pageNum:this.pageNum
                }).then(data=> {
                        console.log("删除后打印的数据",data.data.data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                    
            });    

        }, 

       

      deleteRow(index, rows) {
        
        console.log("1111",rows[index].id)
        postRequest(url.userDelete, {
                   id:rows[index].id,
                   pageNum:this.currentPage
                }).then(data=> {
                        console.log("删除后打印的数据",data.data.data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                    
        });    
        
        
      },
// f分页
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        postRequest(url.userFindAll, {
                   keyword: this.search,
                   pageNum: val, 
                }).then(data=> {
                        console.log("打印的数据",data.data.data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                         
                        
                });      
      },


         goSearch(){//搜索
                postRequest(url.userFindAll, {
                   keyword: this.search,
                }).then(data=> {
                        console.log("打印的数据",data.data.data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                         
                        
                });       

            },
    },
    created(){

            this.curentUserName=getCookie("username")
            postRequest(url.userFindAll, {
                keyword: this.search,
                }).then(data=> {
                        console.log("打印的数据",data.data.data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                        
            });       
        
    }

    
}
</script>




