<template>
    <div class="userMessage  content">
     <!-- tiitle栏 -->
        <div style="padding-bottom:30px">
     
         <div class="top-title" style="background: #66B1ff; padding-left:2%; line-height: 50px;">
                <span class="el-icon-info"></span><span>问卷调查</span><span>/商务宴会调查问卷</span>
         </div>
        </div>

    <!-- 搜索栏 -->
        <div class="top-search" style="text-align: center">
            <el-input style="margin: 0 auto; width:50%;" placeholder="请输入内容"   v-model="search" class="input-with-select">
                
                <el-button slot="append" @click="goSearch()" icon="el-icon-search"></el-button>
            </el-input>
        </div>
    <!-- 新增用户 -->
        <div  class="button-add" style=" display:flex;  justify-content: space-between; overflow: hidden; margin-top:20px; margin-left:2%; margin-right:6%">
         <el-button  style=" margin-right:5%;margin-bottom:20px" type="primary"  @click="deletMost()">批量删除</el-button>  
        </div>

        <el-table
            ref="multipleTable"
            :data="tableList"
            tooltip-effect="dark"
            style="width: 96%;margin:0 auto"
            border
            :header-cell-style="{background:'#EEF1F6'}"
            size="small"
            @selection-change="handleSelectionChange">
            <el-table-column
            type="selection"
           >
            </el-table-column>
           <el-table-column
            type="index"
           >
            </el-table-column>


            <el-table-column
            prop="name"
            label="用户名"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="phone"
            label="手机号"
            show-overflow-tooltip>
            </el-table-column>
           

            <el-table-column
            prop="numberOfPeople"
            label="参加会议人数"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="budget"
            label="您的预算"
           >
            </el-table-column>

             <el-table-column
            prop="meetingDemand"
            label="会议需求"
            >
            </el-table-column>

            <el-table-column
            prop="dateTime"
            label="创建时间"
            >
            </el-table-column>

            <el-table-column
            fixed="right"
            label="操作"
            >
            <template slot-scope="scope">
                <el-button
                @click.native.prevent="deleteRow(scope.$index, tableList)"
                type="text"
                size="small">
                删除
                </el-button>
                
            </template>
            </el-table-column>
        </el-table>
<!-- 分页 -->
          <div class="block" style="overflow:hidden">

                <el-pagination
                style="float:right; margin-right:10%"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="navigatepageNums"
                :page-size="100"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
                </el-pagination>
            </div>




        
    </div>
</template>
<script>

import url from '../utils/url';
import {getCookie} from '../utils/utils';
import {postRequest} from '../utils/api';
export default {
    data(){
        return{
            search:"",
            popTitle:"",
            popsubName:"",
            changeIndex:"",
            currentPage: 1,
            navigatepageNums:[],

            total:1,
            

            ruleForm: {
            username: '',
            nickname:'',
            },
            dialogVisible:false,
            tableList: [],
            multipleSelection: []
        }
    },
    methods:{


        handleSelectionChange(val) {
        this.multipleSelection = val;
        },
        
        
  





    

        deletMost(){
            console.log("查看表单内的选中的条数",this.multipleSelection)
            let chooseStrId="";
            let chooseStrPictures="";
            for(let i=0;i<this.multipleSelection.length;i++){

                chooseStrId+=this.multipleSelection[i].id+","
            }
            chooseStrId = chooseStrId.substr(0, chooseStrId.length - 1);
            console.log("删除的chooseStrId",chooseStrId);
            postRequest(url.bqaDelete, {
                   id:chooseStrId,
                   pageNum:this.currentPage
                }).then(data=> {
                        console.log("删除后打印的数据",data.data.data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                    
            });    

        }, 

       

      deleteRow(index, rows) {
        
        console.log("1111",rows[index].id)
        postRequest(url.bqaDelete, {
                   id:rows[index].id,
                   pageNum:this.currentPage
                }).then(data=> {
                        console.log("删除后打印的数据",data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                    
        });    
        
        
      },
// f分页
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        postRequest(url.bqaFindAll, {
                   keyword: this.search,
                   pageNum: val, 
                }).then(data=> {
                        console.log("打印的数据",data.data.data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                         
                        
                });      
      },


         goSearch(){//搜索
                postRequest(url.bqaFindAll, {
                   keyword: this.search,
                }).then(data=> {
                        console.log("打印的数据",data.data.data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                         
                        
                });       

            },
    },
    created(){
            postRequest(url.bqaFindAll, {
                keyword: this.search,
                }).then(data=> {
                        console.log("打印的数据",data.data.data)
                        let datas=data.data.data;
                        this.tableList=datas.list
                        this.total= datas.pages        
                        this.navigatepageNums=datas.navigatepageNums      
                        this.currentPage=datas.pageNum 
                        
            });       
        
    }

    
}
</script>




