import home from '../components/home.vue';
import login from '../components/login.vue';





import userMessage from '../components/userMessage.vue';
import qBaby from '../components/qBaby.vue';
import qActivity from '../components/qActivity.vue';
import qBusiness from '../components/qBusiness.vue';
import qWedding from '../components/qWedding.vue';

import error from '../components/error.vue';












let routes=[
  {path:'/home',name:"home",component:home},
 
  
  {path:'/login',name:"login",component:login},



  {path:'/userMessage',name:"userMessage",component:userMessage},
  {path:'/qBaby',name:"qBaby",component:qBaby},
  {path:'/qActivity',name:"qActivity",component:qActivity},
  {path:'/qBusiness',name:"qBusiness",component:qBusiness},
  {path:'/qWedding',name:"qWedding",component:qWedding},
  
  
  
  
  
  {path:'/',redirect: '/login'},
  {path:'*',component:error}
]



// export default routes;
export default {routes}


