export default {
  increment:(state)=>{
    state.count++; //突变
  },
  SHOW_FOOT:(state)=>{
    state.bFoot=true;
  },
  HIDE_FOOT:(state)=>{
    state.bFoot=false;
  },
  SHOW_LOADING:(state)=>{
    state.bLoading=true;
  },
  HIDE_LOADING:(state)=>{
    state.bLoading=false;
  }
};



